import PropTypes from 'prop-types'
import React, { useEffect, useLayoutEffect, useRef, useState } from 'react'
import {
  LeftPlayerHand,
  OwnHand,
  PartnerHand,
  RightPlayerHand,
} from '../../styles'
import Hand from '../Cards/Hand'

const sleep = m => new Promise(r => setTimeout(r, m))

const DEAL_CARD_DELAY = 150
const players = ['OWN', 'RIGHT', 'PARTNER', 'LEFT']
const getNext = current =>
  players[players.findIndex(el => el === current) + 1] || players[0]

let debounceTimeout = false
const Dealer = props => {
  const { cards, first, onFinishDealing } = props
  const cardsLengthRef = useRef(0)
  const [leftOffset, setLeftOffset] = useState(0)
  const [isNextPhase, setIsNextPhase] = useState(false)
  const [isFinished, setIsFinished] = useState(false)
  const [currentPlayer, setCurrentPlayer] = useState(first)
  const [currentCard, setCurrentCard] = useState(0)
  const [hands, setHands] = useState({
    OWN: [],
    LEFT: [],
    RIGHT: [],
    PARTNER: [],
  })

  useEffect(() =>
      window.addEventListener('resize',() => {
        clearTimeout(debounceTimeout);
        // start timing for event "completion"
        debounceTimeout = setTimeout(calcOffset, 300);
      }),
    [])

  useEffect(() => {
      cardsLengthRef.current = cards.length
      setIsFinished(false)
      setCurrentCard(cards.length === 5 ? 0 : 4)
      calcOffset()
      dealCards()
    }
    , [cards])

  useEffect(() => {
    if (isFinished && onFinishDealing)
      onFinishDealing()
  }, [isFinished])

  useEffect(() => {
    if (!isFinished)
      setHands({
        ...hands,
        [currentPlayer]: hands[currentPlayer].concat(
          cards[currentCard] || { suit: 'hearts', cardValue: 7 }),
      })

  }, [currentCard])

  useEffect(() => {
    if (!isNextPhase) {
      setCurrentCard(cards.length === 5 ? 0 : 5)
    } else
      setCurrentCard(3)
  }, [currentPlayer, isNextPhase])

  async function dealCards () {
    for (let i = 0; i < players.length; i++) {
      for (let j = 0; j < 3; j++) {
        setCurrentCard(prev => prev + 1)
        await sleep(DEAL_CARD_DELAY)
      }
      if (i === players.length - 1)
        break // dont change the player on the last iteration

      setCurrentPlayer(prev => getNext(prev))
    }

    // Stop here if we are dealing the last three cards
    if (cards.length === 8)
      return

    setIsNextPhase(true)
    setCurrentPlayer(prev => getNext(prev))

    for (let i = 0; i < players.length; i++) {
      for (let j = 0; j < 2; j++) {
        setCurrentCard(prev => prev + 1)
        await sleep(DEAL_CARD_DELAY)
      }
      setCurrentPlayer(prev => getNext(prev))
    }

    setIsFinished(true)
    setIsNextPhase(false)
  }

  const calcOffset = () => {
    const cardsAmount = cardsLengthRef.current
    const screenWidth = document.documentElement.clientWidth
    const vw = screenWidth / 100
    const cardsOffset = 3 * vw
    const cardWidth = Math.min(110, Math.max(8 * vw, 72))
    const handWidth = (cardWidth - cardsOffset) * cardsAmount + cardsOffset

    setLeftOffset((screenWidth - handWidth) / 2)


  }

  const { OWN, LEFT, RIGHT, PARTNER } = hands
  return (
    <React.Fragment key='wrapper'>
      <PartnerHand key='partner'>
        {
          PARTNER &&
          <span style={{ display: 'flex' }}>
          <Hand cards={PARTNER} leftOffset={leftOffset}/>
              </span>
        }
      </PartnerHand>
      <LeftPlayerHand key='left'>
        {
          LEFT &&
          <Hand key='lef' cards={LEFT} leftOffset={leftOffset} />
        }
      </LeftPlayerHand>
      <RightPlayerHand key='right'>
        {
          RIGHT &&
          <Hand cards={RIGHT} leftOffset={leftOffset} />
        }
      </RightPlayerHand>
      <OwnHand size={cards.length} key='self'>
        {
          OWN &&
          <Hand key='own' cards={OWN} isOwnHand leftOffset={leftOffset}/>
        }
      </OwnHand>
    </React.Fragment>
  )
}

Dealer.propTypes = {
  cards: PropTypes.array.isRequired,
  first: PropTypes.oneOf(['OWN', 'PARTNER', 'LEFT', 'RIGHT']).isRequired,
}

export default Dealer