import styled from 'styled-components'
import { Typography } from '@rmwc/typography'
import { IconButton } from '@rmwc/icon-button'

import '@material/icon-button/dist/mdc.icon-button.css'

//If something is buggy here and title is not correct, maybe you put it in separate page, without importing Typography styles from rmwc.io
export const FooterWrapper = styled.footer`
  margin-top:25vh;
  position:relative;
  background:#21212190;
  width: 100%;
`

export const ContributorsSection = styled.section`
   display:flex;
   flex-direction:column;
   padding:10px 15px;
`

export const ContributorsTitle = styled(Typography)`
  text-align:center;
  padding:15px 0; 
`

export const LogoButton = styled.a`
  &:hover {
    background: rgba(255,255,255,0.05);
  }
`

export const LogosContainer = styled.div`
  user-select:none;
  max-width:90%;
  margin:auto;
  display:flex;
  align-items:baseline;
  flex-wrap:wrap;
  & :nth-child(n) {
    margin:15px;
  }
`

export const LogoImage = styled.img`
  max-width: 100%;
  height: auto;
  ${
  props => {
    const { width, height } = props

    return `
        width: ${width || 'inherit'}px;
        height: ${height || 'inherit'}px;
      `
  }
}
`

export const FixedSubFooter = styled.div`
  width:100%;
  background: #00000099;
  padding:15px 0;
  text-align:center;
`

export const SocialMediaIcon = styled(IconButton)`
`

export const CopyrightText = styled.p`
  color:white;
  margin:auto;
  vertical-align:center;
`