import styled from 'styled-components'
import { Tooltip } from '@rmwc/tooltip'
import { Avatar } from '@rmwc/avatar'
import { Button } from '@rmwc/button'
import { Typography } from '@rmwc/typography'

import '@material/typography/dist/mdc.typography.css'
import '@rmwc/tooltip/tooltip.css'


export const ToolTipWrapper = styled(Tooltip)`
  opacity:1!important;
  font-size:1.1rem!important;
  max-width:35%;
`

export const TooltipContentWrapper = styled.div`
  background: ${({ theme }) => theme.surface || '#FFF'}
  color: rgba(0,0,0,.87);
  padding:15px 10px;
`

export const Title = styled(Typography)`
  font-family: 'Old Standard TT', serif;
  font-style:oblique;
  font-weight:bolder;
  text-align:center;
  padding:5px 0;
  color: ${({ localtheme }) => localtheme.onSurface}};
`

export const SubTitle = styled.div`
  margin-top:20px;
  padding-left:10px;
  text-align:center;
  float:right;
  display:flex;
  flex-direction:column;
`

export const Date = styled(Typography)`
  font-size:0.6em;
  font-family:"Roboto sans-serif";
  font-style:italic;
  font-weight:500;
  margin-top:-6px;
  color: ${({ localtheme }) => localtheme.textDisabledOnBackground}};

`

export const Author = styled(Typography)`
  font-family:"Roboto sans-serif";
  text-wrap:wrap;
  font-size:0.7em;
  font-style:italic;
  font-weight:500;
  color: ${({ localtheme }) => localtheme.textDisabledOnBackground}};

`

export const AuthorPhoto = styled(Avatar)`
  margin:auto;
  width:60px;
  height:60px;
`

export const Content = styled.p`
  font-family: 'IBM Plex Sans', sans-serif;
  font-style:italic;
  font-weight:700;
  text-align:justify;
  min-height:100px;
  padding:10px;
  color: ${({ localtheme }) => localtheme.onSurface}};

`

export const OpenInNewPage = styled(Button)`
  &:before, &:after{
  ${({localtheme}) => ({
    backgroundColor: localtheme.secondary + '!important;'   
})}
}
  margin-top:10px;
  font-family: 'Old Standard TT', serif;
  font-weight:bolder;
  float:right;
`



