import { UPDATE_THEME } from 'reducers/context'
import { theme } from 'utils/themeUtils'

export default store => next => action => {

  if ( action.type === UPDATE_THEME ) {
    //action.payload = themeName (default | material | dark )
    action.payload = { ...theme[action.payload], name: action.payload }
  }

  next(action)
}