import React, { Suspense, useContext } from 'react'
import { Redirect, Route, Router, Switch } from 'react-router-dom'
import { ThemeProvider } from '@rmwc/theme'
import ThemeContext from 'context/ThemeContext'
import withContext from 'context/withContext'
import { Loading } from 'components/common'
import Footer from 'components/footer/Footer'
import browserHistory from 'utils/browserHistory'
import { NavBar } from './components/navigation'
import MainPage from './pages/MainPage'
import RulesPage from './pages/RulesPage'
import BelotGamePage from './pages/BelotGamePage'
import './normalize.css'

// I want to give React.lazy a chance, but it messes up my theming
// still can be done with a workaround but FB says its experimental
// const MainPage = React.lazy(() => import('pages/MainPage'))
// const RulesPage = React.lazy(() => import('pages/RulesPage'))

function InitializationLayer () {
  const theme = useContext(ThemeContext)

  return (
    <Router history={browserHistory}>
      <ThemeProvider className='mdc-theme--background' options={theme}>
        <NavBar/>
        <main style={{ height: '100vh' }}>
          <Suspense fallback={<Loading/>}>
            <Switch>
              <Route exact path='/' component={MainPage}/>
              <Route exact path='/rules' component={RulesPage}/>
              <Route exact path='/game' component={BelotGamePage}/>
              <Redirect to='/'/>
            </Switch>
          </Suspense>
        </main>
        <Footer/>
      </ThemeProvider>
    </Router>
  )
}

export default withContext(InitializationLayer)
