import React from 'react'
import styled from 'styled-components'
import { Card } from '@rmwc/card'

export const CardWrapper = styled(
  ({ index, vertical, leftOffset, ...rest }) => <Card {...rest} />)`
${({ index, vertical, leftOffset }) => {
  return vertical
    ? {
      // transform: `translateY(calc(${leftOffset}px - ${index * 3}vw));`,
      // top: 'calc(-50% - ' + index * 3 + 'vw);',
      // left: -(index * 3) + 'vw;'
    }
    : {
      transform: `translate(calc(${leftOffset}px - ${index * 3}vw));`,
      // transform: 'translateX(-' + index * 3 + 'vw)',
      // left: 'max(35vw, 200px, 150px);'
    }
}
}
    ${(props) =>
  `&.cards-${props.index}-appear {
        transform: translateX(70vw);
      }
      &.cards-${props.index}-appear-active {
        transition:all 1.2s ease;
        transform: translate(calc(${props.leftOffset}px - ${props.index * 3}vw)); 
      }`
}
    
    position:relative;
    min-width:72px;
    max-width:110px;
    min-height:100px;
    max-height:152px;
    width: 8vw;
    height: 11vw;
    transform-style: preserve-3d;
    transition:all .8s linear;
`

export const CardFace = styled.div`
  background-image: ${({ url }) => 'url(' + url + ')'};
  position: absolute;
  left:0;
  top:0;
  width: 100%;
  height:100%;
  background-size: cover;
`

export const CardBack = styled.div`
  background-image: ${({ url }) => 'url(' + url + ')'};
  position: absolute;
  left:0;
  top:0;
  width: 100%;
  height:100%;
  background-repeat: no-repeat;
  background-size: cover;
  backface-visibility: hidden;
`