import React from 'react'
import {
  BriefInfoTypography,
  BriefInfoWrapper,
  ReadMoreButtonWrapper,
  ReadMoreLink,
} from './styles'

const BriefInfo = ({ info }) => (
  <BriefInfoWrapper>
    <BriefInfoTypography use='body1' theme='onSurface'>
      Magic Belot is like a normal belot, but with magic cards,
    </BriefInfoTypography>
    <ReadMoreButtonWrapper theme='onSurface'>
      <ReadMoreLink to='rules'>
        read the full article
      </ReadMoreLink>
    </ReadMoreButtonWrapper>
  </BriefInfoWrapper>
)

export default BriefInfo