import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { ThemeProvider } from '@rmwc/theme'
import ThemeContext from 'context/ThemeContext'
import { formatDate } from 'utils/dateUtils'
import { truncate } from 'utils/TextUtils'

import '@material/theme/dist/mdc.theme.css'
import {
  Author,
  AuthorPhoto,
  Content,
  Date,
  OpenInNewPage,
  SubTitle,
  Title,
  TooltipContentWrapper,
  ToolTipWrapper
} from './styles'

const photoUrl = 'https://img.freepik.com/free-vector/businessman-profile-cartoon_18591-58479.jpg?size=338&ext=jpg'

const PostTooltip = ({ data, children }) => {
  const theme = useContext(ThemeContext)
  const { id, title, content, createdBy, dateCreated } = data

  const renderContent = () => (
    <TooltipContentWrapper
      theme={theme}
    >
      <ThemeProvider
        options={{
          ...theme,
        }}>
        <Title use='headline5' tag='p' localtheme={theme}>
          {title}
        </Title>
        <SubTitle>
          <AuthorPhoto
            contain
            src={photoUrl}
            name={createdBy}
            size='small'
          />
          <Author use='caption' tag='p' localtheme={theme}>
            {createdBy}
          </Author>
          <Date use='caption' tag='p' localtheme={theme}>
            {formatDate(dateCreated)}
          </Date>
        </SubTitle>
        <Content localtheme={theme}>
          {truncate(content, 150)}
        </Content>
        <Link to={`/posts/${id}`}>
          <OpenInNewPage
            localtheme={theme}
            theme={['secondary']}
            trailingIcon="keyboard_arrow_right"
          >
            Open in separate page
          </OpenInNewPage>
        </Link>
      </ThemeProvider>
    </TooltipContentWrapper>
  )

  return (
    <ToolTipWrapper
      content={renderContent()}
      activateOn='click'
      showArrow
      align='left'
    >
      {children}
    </ToolTipWrapper>
  )
}

export default PostTooltip