import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { List, ListDivider, ListGroup, ListGroupSubheader, SimpleListItem } from '@rmwc/list'
import { Avatar } from '@rmwc/avatar'

import '@material/menu/dist/mdc.menu.css'
import '@material/menu-surface/dist/mdc.menu-surface.css'
import '@material/list/dist/mdc.list.css'
import '@rmwc/avatar/avatar.css'

export const DrawerListItems = styled(List)``

export const DrawerItem = styled(SimpleListItem)`
  user-select:none;
  
  & > i:first-child {
    margin-right: 15px;
    color:${({ localtheme }) => localtheme.textIconOnDark}!important;
  }
  
  &:after{
  ${({ localtheme /*RIPPLE*/ }) => ({
  backgroundColor: localtheme.primary + '!important;',
})}
  }
  
  &:before{
  ${({ localtheme /*HOVER*/ }) => ({
  backgroundColor: localtheme.primary + '!important;',
})}
  }
  
  }
  &:not(.mdc-list-item--disabled).mdc-list-item--activated::before, :not(.mdc-list--non-interactive) > :not(.mdc-list-item--disabled).mdc-list-item--activated::after{
    background-color:${({ localtheme }) => localtheme.accent + '!important;'}
  }
  
  &.mdc-list-item--activated{
     color:${({ localtheme: { accent } }) => accent + '!important;'}
     & > i {
         color:${({ localtheme: { accent } }) => accent + '!important;'}
     }
  }
`

export const DrawerListDivider = styled(ListDivider)``

export const DrawerUserIcon = styled(Avatar)``

export const DrawerLink = styled(Link)`
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
`

export const DrawerListGroup = styled(ListGroup)`
  font-family: 'IBM Plex Sans', sans-serif;

`

export const DrawerListGroupHeader = styled(ListGroupSubheader)`
  &.mdc-list-group__subheader {
    fontFamily: 'IBM Plex Sans', sans-serif;
  }
`