import React, { useState } from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import Icon from '@mdi/react'
import AnnounceMetaData from './AnnounceMetaData'
import { ANNOUNCE } from '../../../../../utils/ActionType'
import MagicCard from '../../../../MagicCard'

import {
  Announces,
  AnnounceList,
  AnnounceSingle,
  AnnounceWrapper,
  ButtonWrapper,
  ConfirmButton,
  Overlay,
  MagicCardHolder,
} from './styles'
import './animations.scss'

const Announce = ({ announces, onAnnounce }) => {
  const { availableAnnounces, availableMagicCards } = announces

  const [selected, setSelected] = useState(null)
  const [selectedMagicCard, setSelectedMagicCard] = useState({})

  function confirmAnnounce () {
    setSelected(null)
    setSelectedMagicCard({})
    onAnnounce({
      actionType: ANNOUNCE,
      announceType: selected,
      magicCard: selectedMagicCard,
    })
  }

  function renderAnnounces () {
    return Object.keys(availableAnnounces).map(announce => {
      const { isMdi = false, icon = '', color = 'inherit' } = AnnounceMetaData[announce] ||
      {}
      return (
        <AnnounceSingle
          key={announce}
          color={color} //TODO: This will be reworked
          graphic={isMdi ? <Icon path={icon}/> :
            <img src={icon} style={{ 'width': '45px', height: '45px' }}
                 alt={announce}/>}
          disabled={!availableAnnounces[announce]}
          text={announce}
          selected={selected === announce}
          onClick={availableAnnounces[announce]
            ? () => setSelected(announce)
            : null}
        />
      )
    })
  }

  function toggleMagicCardSelect (isSelected, card) {
    // Object with key - the magic card, value - isSelected ( need to implement on backend)
    setSelectedMagicCard({
      ...selectedMagicCard,
      [card]: isSelected,
    })
  }

  return (
    <Announces>
      <Overlay/>
      <MagicCardHolder
        transitionName="left-magic-card"
        transitionAppear={true}
        transitionAppearTimeout={3000}
        transitionEnter={false}
        transitionLeaveTimeout={2300}
      >
        <MagicCard
          key='left_card'
          onSelect={toggleMagicCardSelect}
          card='DESTINY_CREATOR'
          isDisabled={!availableMagicCards['DESTINY_CREATOR']}/>
      </MagicCardHolder>
      <ReactCSSTransitionGroup
        component={React.Fragment}
        transitionName="announces"
        transitionAppear={true}
        transitionAppearTimeout={3000}
        transitionEnter={false}
        transitionLeaveTimeout={2300}>
        <AnnounceWrapper z={9}>
          <AnnounceList>
            {renderAnnounces()}
          </AnnounceList>
          <ButtonWrapper>
            <ConfirmButton
              raised
              disabled={!selected}
              onClick={confirmAnnounce}
              label='confirm'
            />
          </ButtonWrapper>
        </AnnounceWrapper>
      </ReactCSSTransitionGroup>
      <MagicCardHolder
        transitionName='right-magic-card'
        transitionAppear={true}
        transitionAppearTimeout={3000}
        transitionEnter={false}
        transitionLeave={false}
      >
        <MagicCard
          key='right_card'
          onSelect={toggleMagicCardSelect}
          card='BLIZZARD_STORM'
          isDisabled={!availableMagicCards['BLIZZARD_STORM']}/>
      </MagicCardHolder>
    </Announces>
  )
}

export default Announce