import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom'
import PropTypes from 'prop-types'

const SCROLL_THRESHOLD = 150

//TODO: Implement when connecting to backend and redux
const InfiniteScroll = ({ loadDataFunction, children, parentRef, scrollThreshold = SCROLL_THRESHOLD }) => {
  // const [ loadMore, setLoadMore ] = useState(false)

  const handleScroll = ev => {
    const target = ReactDOM.findDOMNode(ev.target)
    const { clientHeight, scrollTop } = target
    if (clientHeight - scrollTop < scrollThreshold) {
      loadDataFunction()
    }

  }

  useEffect(() => {
    // const { current } = parentRef
    // if(!current)
    //   return
    // current.addEventListener('scroll', handleScroll)
    // return () => current.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <>
      {children}
    </>
  )
}

InfiniteScroll.propTypes = {
  parentRef: PropTypes.oneOfType([
      // Either a function
      PropTypes.func,
      // Or the instance of a DOM native element (see the note about SSR)
      PropTypes.shape({ current: PropTypes.instanceOf(Element) })
    ]).isRequired,
  loadDataFunction: PropTypes.func.isRequired,
}

export default InfiniteScroll