import styled from 'styled-components'
import { ListItem, ListItemMeta, ListItemPrimaryText, ListItemSecondaryText, ListItemText } from '@rmwc/list'
import '@material/list/dist/mdc.list.css'

export const Wrapper = styled(ListItem)`
  user-select:none;
  font-family: 'Old Standard TT', serif;
  padding-top:5px!important;
  padding-bottom:15px!important;

`

export const TextWrapper = styled(ListItemText)`
`

export const PostTitle = styled(ListItemPrimaryText)`
  font-size: 1.2rem;
  color: ${({ localtheme }) => localtheme.primary}!important;
`

export const PostContent = styled(ListItemSecondaryText)`
  font-family: 'Roboto Slab', serif;
  color: ${({ localtheme }) => localtheme.primary + 'bb'}!important;

`

export const PostMetaData = styled(ListItemMeta)`
`
