import React from 'react'
import { LogoButton, LogoImage, LogosContainer } from './styles'

const reactLogo = require('assets/logos/react-logo.png')
const reduxLogo = require('assets/logos/redux-logo.png')
const materialLogo = require('assets/logos/material-logo.svg')
const styledComponentsLogo = require('assets/logos/styled-components-logo.png')
const reactRouterLogo = require('assets/logos/react-router-logo.png')

const TechnologiesSection = () => (
  <LogosContainer>
    <LogoButton href='http://reactjs.com'>
      <LogoImage height={100} src={reactLogo} />
    </LogoButton>
    <LogoButton href='https://redux.js.org/'>
      <LogoImage width={130} src={reduxLogo}/>
    </LogoButton>
    <LogoButton href='http://material.io'>
      <LogoImage width={130} src={materialLogo}/>
    </LogoButton>
    <LogoButton href='https://reacttraining.com/react-router/web/guides/quick-start'>
      <LogoImage height={95} src={reactRouterLogo}/>
    </LogoButton>
    <LogoButton href='https://www.styled-components.com/'>
      <LogoImage width={80} src={styledComponentsLogo}/>
    </LogoButton>
  </LogosContainer>
)

export default TechnologiesSection