import React, { useEffect, useRef } from 'react'
import PropTypes from 'prop-types'
import { CardBack, CardFace, CardWrapper } from './styles'
import { CardPrimaryAction } from '@rmwc/card'

const background = require(`../../../../../../assets/cards/card-back.png`)

const Card = ({ suit, value, index, isVisible, isVertical, leftOffset }) => {
  const imageRef = useRef()

  useEffect(() => {
    if (!imageRef.current)
      imageRef.current = require(
        `../../../../../../assets/cards/${value}-${suit}.png`)
  }, [])

  return (
    <CardWrapper index={index} vertical={isVertical} leftOffset={leftOffset}>
      {isVisible ?
        <>
          <CardBack url={background}/>
          <CardFace url={imageRef.current}>
            <CardPrimaryAction style={{ width: '100%', height: '100%' }}/>
          </CardFace>
        </>
        :
        <>
          {/*<CardFace url={img}/>*/}
          <CardBack url={background}/>
        </>
      }
    </CardWrapper>
  )
}

Card.propTypes = {
  suit: PropTypes.oneOf(['CLUBS', 'DIAMONDS', 'HEARTS', 'SPADES']).isRequired,
  value: PropTypes.oneOf([7, 8, 9, 10, 'J', 'Q', 'K', 'A']).isRequired,
  isVisible: PropTypes.bool,
  translateX: PropTypes.number,
  translateY: PropTypes.number,
  translateZ: PropTypes.number,
  rotateX: PropTypes.number,
  rotateY: PropTypes.number,
  rotateZ: PropTypes.number,
}

export default Card