import styled from 'styled-components'
import { List, ListDivider } from '@rmwc/list'
import { Elevation } from '@rmwc/elevation'

export const PostsTitle = styled.h3`
  text-align:center;
  font-size:1.25rem;
  text-transform:uppercase;
  font-family: 'Old Standard TT', serif;
  font-style:oblique;
  user-select:none;
  padding:25px 10px 15px;
  ${({ localtheme }) => {
  const { primary, onPrimary } = localtheme
  return `
      background: ${primary};
      color: ${onPrimary}
    `
}}
`

export const PostsList = styled(List)`
  padding-top:0!important;
  padding-bottom:0!important;
`

export const PostsDivider = styled(ListDivider)`
`

export const PostsWrapper = styled(Elevation)`
  background-color:${({ localtheme }) => localtheme.backgroundLight}!important;
  overflow:hidden;
`

export const Scroll = styled.div`
  overflow-y: scroll;
  height:45em;
  &::-webkit-scrollbar {
    width: 2px;
  }
  &::-webkit-scrollbar-thumb {
    background: ${({ localtheme }) => localtheme.primary};
  }
`