import React, { useEffect, useRef, useState } from 'react'
import ReactDOM from 'react-dom'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import { mdiFullscreen, mdiFullscreenExit } from '@mdi/js'
import Icon from '@mdi/react'
import Announce from './components/Announce'
import Hand from './components/Cards/Hand'
import Dealer from './components/Dealer'
import { GameFieldDefaultProps, GameFieldPropTypes } from './propTypes'
import useFullscreenStatus from './utils/FullScreenUtils'
import {
  FullScreenButton,
  GameFieldWrapper,
  LeftPlayerHand,
  OwnHand,
  PartnerHandHolder,
  RightPlayerHand,
} from './styles'
import './game-field-animations.scss'

const GameField = ({ announces, cards, onUserAction }) => {
  const [verticalHandPosition, setVerticalHandPosition] = useState(
    { left: {}, right: {} })
  const [isDealing, setIsDealing] = useState(true)

  const gameFieldRef = useRef()
  const leftPlayerRef = useRef()
  const rightPlayerRef = useRef()

  const [isFullscreen, setIsFullscreen] = useFullscreenStatus(gameFieldRef)

  useEffect(() => {
      const position = {}
      // position.left = ReactDOM.findDOMNode(
      //   leftPlayerRef.current).
      //   getBoundingClientRect()
      // position.right = ReactDOM.findDOMNode(
      //   rightPlayerRef.current).
      //   getBoundingClientRect()
      // setVerticalHandPosition(position)
    },
    [announces])

  function dealingFinished () {
      setIsDealing(false)
  }
  //
  // const { right: { width, height } } = verticalHandPosition
  // const cardWidth = height / cards.length
  // const vw = document.body.clientWidth / 100
  // const totalWidth = height - ((cards.length - 1) * (3 * vw))

  return (
    <GameFieldWrapper ref={gameFieldRef}>
      <FullScreenButton
        checked={isFullscreen}
        onChange={() => setIsFullscreen(!isFullscreen)}
        icon={<Icon size={2} path={mdiFullscreen}/>}
        onIcon={<Icon size={2} path={mdiFullscreenExit}/>}
      />
      <ReactCSSTransitionGroup
        component={React.Fragment}
        transitionName="announce-animation"
        transitionEnter={false}
        transitionLeaveTimeout={400}>
        {
          announces && !isDealing &&  //There are announces available and the dealer isn't dealing cards
          <Announce key='announce' announces={announces} onAnnounce={onUserAction}/>
        }
      </ReactCSSTransitionGroup>
      {
        cards &&
          <Dealer onFinishDealing={dealingFinished} cards={cards} first='OWN'/>
      }
    </GameFieldWrapper>
  )
}

GameField.propTypes = GameFieldPropTypes

GameField.defaultProps = GameFieldDefaultProps

export default GameField