import { List, SimpleListItem } from '@rmwc/list'
import { Typography } from '@rmwc/typography'
import styled from 'styled-components'

export const SingleNewWrapper = styled.div`
  display:flex;
  flex-direction:column;
`

export const NewsTitle = styled(Typography)`
  margin-top:5px;
  text-align:center;
  font-family: 'Playfair Display', serif!important;
`

export const NewsListView = styled(List)`
  font-family: 'Old Standard TT', serif!important;
`

export const NewsListItem = styled(SimpleListItem)`
  cursor:default;
  height:35px!important;
  font-weight:bold;
  & > i {
    user-select:none;
    margin-right: 15px;
  }
`

export const NewsDate = styled(Typography)`
  margin:-5px 0 5px!important;
  font-size:0.7rem;
  text-align:center;
  font-family: 'Playfair Display', serif!important;
`