import styled from 'styled-components'
import {
  Card,
  CardActionButton,
  CardActionButtons,
  CardActions,
  CardMedia, CardPrimaryAction,
} from '@rmwc/card'
import { Typography } from '@rmwc/typography'

import '@material/card/dist/mdc.card.css'
import '@material/button/dist/mdc.button.css'
import '@material/icon-button/dist/mdc.icon-button.css'

export const CardWrapper = styled(Card)`
  position:relative;
  transition:box-shadow .5s ease;
  background:#FFF!important;
  ${({ size, lettersize, selected }) => ({
  width: size,
  fontSize: lettersize,
  boxShadow: selected && '0px 0px 35px gold!important;',
})}
`

export const MagicCardPrimaryAction = styled(CardPrimaryAction)`
  height:100%;
`

export const CardPicture = styled(CardMedia)`
  background-image: ${({ image }) => `url(${image})`};
`
/*${({ localtheme }) => ({
  background:localtheme.background,
})}*/
export const CardInfoWrapper = styled.div`
  padding:.8em 15px;
`

export const CardName = styled(Typography)`
  color:#121212;
  font-size:.85em!important;
  white-space: nowrap;
`

export const CardSubtitle = styled(Typography)`
  color:#a1a1a1;
  font-size:.60em!important;
  margin-top:-.3em;
  font-style:italic;
`

export const CardDescription = styled(Typography)`
  font-family:Roboto slap!important;
  font-style:italic;
  color:rgba(0,0,0,.76);
  margin-top:.9em;
  font-size:1.1em!important;
`

export const MagicCardActions = styled(CardActions)`
`
export const MagicCardActionsButtons = styled(CardActionButtons)``

export const MagicCardActionsButton = styled(CardActionButton)``

//SVG IDEA TAKEN FROM https://stackoverflow.com/a/28974253
export const DisabledOverlay = styled.div`
  width:100%;
  height:100%;
  top:0;
  left:0;
  position:absolute;
  z-index:999;
  background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' preserveAspectRatio='none' viewBox='0 0 100 100'><path d='M1 0 L0 1 L99 100 L100 99' fill='red    ' /><path d='M0 99 L99 0 L100 1 L1 100' fill='red' /></svg>");
  background-repeat:no-repeat;
  background-position:center center;
  background-size: 100% 100%, auto;
  background-color:rgba(0,0,0,.5);
`

