import React, { useContext } from 'react'
import ThemeContext from '../../../../../context/ThemeContext'
import { formatDate } from 'utils/dateUtils'

import {
  Wrapper,
  PostContent,
  PostMetaData,
  PostTitle,
  TextWrapper,
} from './styles'

const SinglePost = ({ data }) => {
  const theme = useContext(ThemeContext)
  const { title, content, dateCreated, createdBy } = data

  return (
    <Wrapper>
      <TextWrapper>
        <PostTitle localtheme={theme}>
          {title}
        </PostTitle>
        <PostContent localtheme={theme}>
          {content}
          <PostMetaData localtheme={theme}>
            on {formatDate(dateCreated)} by {createdBy}
          </PostMetaData>
        </PostContent>
      </TextWrapper>
    </Wrapper>
  )
}

export default SinglePost