import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { GameField } from 'modules/belot'
import { toggleNavIsFixed } from 'reducers/ui'

import { PageLayout } from './styles'

/**
 * This is a wrapper for the Belot game itself,
 * since i may move the game into another project, this will be the connector
 * between this app and the belot (Redux store etc etc)
 *
 */
const BelotGamePage = props => {
  const dispatch = useDispatch()
  const store = useSelector(({ auth: { loggedInUser } }) => ({
    loggedInUser,
  }))

  useEffect(() => {
    dispatch(toggleNavIsFixed(false))
  }, [])

  const { loggedInUser } = store

  return (
    <PageLayout>
      <GameField currentUser={loggedInUser}/>
    </PageLayout>
  )
}

export default BelotGamePage