import React, { useContext, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { withRouter } from 'react-router-dom'
import MaterialDrawer from 'components/common/material/MaterialDrawer'
import { startLogout } from 'reducers/auth'
import { updateTheme } from 'reducers/context'
import { hideDrawer } from 'reducers/ui'
import ThemeContext from 'context/ThemeContext'
import ThemePicker from './components/ThemePicker'

import { IconButton } from '@rmwc/icon-button'
import {
  DrawerItem,
  DrawerLink,
  DrawerListDivider,
  DrawerListGroup,
  DrawerListGroupHeader,
  DrawerListItems,
  DrawerUserIcon
} from './styles'

const getStartOfUrl = url => url.split('/')[1]

const NavigationDrawer = ({ location: { pathname } }) => {
  const theme = useContext(ThemeContext)
  const [ isThemeMenuOpen, setIsThemeMenuOpen ] = useState(false)
  const [ activeNavigation, setActiveNavigation ] = useState(getStartOfUrl(pathname))

  const dispatch = useDispatch()
  const store = useSelector(({ auth: { loggedInUser } }) => ({
    loggedInUser,
  }))

  const { loggedInUser } = store
  const { username, profilePhoto } = loggedInUser

  useEffect(() => {
    setActiveNavigation(getStartOfUrl(pathname))
  }, [ pathname ])

  function toggleThemeMenu() {
    setIsThemeMenuOpen(!isThemeMenuOpen)
  }

  function closeThemeMenu(chosenTheme) {
    if ( chosenTheme )
      dispatch(updateTheme(chosenTheme))
    setIsThemeMenuOpen(false)
  }

  function logout() {
    dispatch(hideDrawer())
    dispatch(startLogout())
  }

  return (
    <MaterialDrawer
      theme={theme}
      title={<DrawerUserIcon
        src={profilePhoto}
        name={username}
        size='xlarge'
      />}
      subtitle={
        `Hello, ${username}`
      }
    >
      <DrawerListDivider/>
      <DrawerListItems>
        <DrawerListGroup>
          <DrawerListGroupHeader>
            Navigation
          </DrawerListGroupHeader>
          <DrawerItem
            activated={activeNavigation === ''}
            routename=''
            localtheme={theme}
            theme='textIconOnDark'
            graphic={
              <IconButton checked={activeNavigation === ''} icon='home' onIcon='forward'/>
            }
            text='MainPage'
          >
            <DrawerLink to='/'/>
          </DrawerItem>

          <DrawerItem
            activated={activeNavigation === 'rules'}
            routename='rules'
            localtheme={theme}
            theme='textIconOnDark'
            graphic={
              <IconButton checked={activeNavigation === 'rules'} icon='description' onIcon='forward'/>
            }
            text='Rules'
          >
            <DrawerLink to='/rules'/>
          </DrawerItem>

          <DrawerItem
            activated={activeNavigation === 'game'}
            routename='game'
            localtheme={theme}
            theme='textIconOnDark'
            graphic={
              <IconButton checked={activeNavigation === 'game'} icon='videogame_asset' onIcon='forward'/>
            }
            text='Game'
          >
            <DrawerLink to='/game'/>
          </DrawerItem>
        </DrawerListGroup>

        <DrawerListDivider/>

        <DrawerListGroup>
          <DrawerListGroupHeader>
            Preferences
          </DrawerListGroupHeader>

          <ThemePicker
            theme={theme}
            currentTheme={theme.name}
            isOpen={isThemeMenuOpen}
            onClose={closeThemeMenu}
            toggle={(
              <DrawerItem
                localtheme={theme}
                theme='textIconOnDark'
                graphic='palette'
                onClick={toggleThemeMenu}
                text='Theme'
                metaIcon='keyboard_arrow_right'
              />
            )}
          />
          <DrawerListDivider/>
          <DrawerListGroupHeader>
            Leaving so soon?
          </DrawerListGroupHeader>

          <DrawerItem
            localtheme={theme}
            onClick={logout}
            theme='textIconOnDark'
            graphic='logout'
            text='Logout'
          />
        </DrawerListGroup>
      </DrawerListItems>
    </MaterialDrawer>
  )
}

export default withRouter(NavigationDrawer)