import ThemeContext from 'context/ThemeContext'
import React, { useContext, useEffect, useState } from 'react'
import SingleNew from './SingleNew'

import { HorizontalLine, NewsSectionTitle, NewsWrapper } from './styles'

const STUB_NEWS = require('./stubs/news.json')

const NewsFeed = props => {
  const theme = useContext(ThemeContext)
  const [news, setNews] = useState([])

  useEffect(() => setNews(STUB_NEWS), [])

  const renderNews = () => (
    news.map(singeNew => <SingleNew key={singeNew.id} data={singeNew}/>)
  )

  return (
    <NewsWrapper z={12} localtheme={theme}>
      <NewsSectionTitle
        tag='p' use='headline4'
        theme={['textPrimaryOnBackground']}
      >
        Updates
      </NewsSectionTitle>
      <HorizontalLine/>
      {renderNews()}
    </NewsWrapper>
  )
}

export default NewsFeed