import React, { useEffect, useState } from 'react'
import { connect, useDispatch } from 'react-redux'
import { TopAppBarFixedAdjust } from '@rmwc/top-app-bar'
import { startLogin, startRegister } from 'reducers/auth'
import { showDrawer } from 'reducers/ui'
import { Loading } from 'components/common'
import { updateTheme } from 'reducers/context'
import NavigationDrawer from '../NavigationDrawer'

import '@material/icon-button/dist/mdc.icon-button.css'
import '@material/top-app-bar/dist/mdc.top-app-bar.css'
import { NavLink, TopAppNavBar } from './styles'

const NavBar = ({
                  user, isLoading, startLogin,
                  startRegister, showDrawer, fixed,
                }) => {
  const dispatch = useDispatch()
  const [ isFixed, setIsFixed ] = useState(fixed)

  //TODO: Remove this
  useEffect(() => {
    login()
    dispatch(updateTheme('crane'))
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => setIsFixed(fixed),
    [ fixed ])

  function toggleNavBarBehaviour() {
    setIsFixed(!isFixed)
  }

  function login() {
    startLogin()
  }

  function register() {
    startRegister()
  }

  function openDrawer() {
    showDrawer()
  }

  const icon = isFixed ? 'unfold_more' : 'unfold_less'

  return (
    <>
      {
        user &&
        <NavigationDrawer/>
      }
      <TopAppNavBar
        shortCollapsed={isFixed}
        short={user && isFixed}
        title='Magic belot'
        navigationIcon={user && {
          onClickCapture: openDrawer,
        }}
        endContent={
          <>
            <NavLink isVisible={!user} href='#' onClick={login}>
              Login
            </NavLink>
            <NavLink isVisible={!user} href='#' onClick={register}>
              Register
            </NavLink>
          </>
        }
        actionItems={user && [
          {
            icon: icon,
            style: {
              transform: 'rotate(90deg)',
              display: 'inline-block',
            },
            onClick: toggleNavBarBehaviour,
          },
        ]}
      />
      {!isFixed &&
      <TopAppBarFixedAdjust/>
      }
      <Loading when={isLoading}/>
    </>
  )
}

const mapStateToProps = ({ auth, ui: { navigation } }) => ({
  user: auth.loggedInUser,
  isLoading: auth.isLoading,
  fixed: navigation.isFixed,
})

const mapDispatchToProps = {
  startLogin,
  startRegister,
  showDrawer,
}

export default connect(mapStateToProps, mapDispatchToProps)(NavBar)