import React, { useState } from 'react'
import PropTypes from 'prop-types'
import {
  CardDescription,
  CardInfoWrapper,
  CardName,
  CardPicture,
  CardSubtitle,
  CardWrapper, DisabledOverlay,
  MagicCardActions,
  MagicCardActionsButton,
  MagicCardActionsButtons,
  MagicCardPrimaryAction,
} from './styles'

const BLIZZARD_STORM = require('assets/belot/magicCards/blizzard_storm.jpeg')
const DESTINY_CREATOR = require('assets/belot/magicCards/destiny-creator.jpg')

const CARDS = {
  BLIZZARD_STORM,
  DESTINY_CREATOR,
}

const CARDS_DESCRIPTION = {
  BLIZZARD_STORM: {
    title: 'Blizzard storm',
    subtitle: 'Freeze your enemies',
    description: 'Prevents other players from announcing after you',
  },
  DESTINY_CREATOR: {
    title: 'Destiny creator',
    subtitle: 'Forge your destiny',
    description: 'Lets you peek one of your next 3 cards, before announcing',
  },
}

const MagicCard = React.forwardRef(
  (
    { card, size, fontSize, ratio, isPreview, isDisabled, onSelect, ...rest },
    ref) => {

    const [isSelected, setIsSelected] = useState(false)

    const isSquare = ratio === 'square'
    const { width, units, __NOT_PROP__ } = size
    const usedWidth = __NOT_PROP__ && isSquare ? width / 1.25 : width
    const cardUrl = CARDS[card]

    const { title, subtitle, description } = CARDS_DESCRIPTION[card]

    function toggleSelect () {
      if (onSelect)
        onSelect(!isSelected, card)
      setIsSelected(!isSelected)
    }

    return (
      <CardWrapper
        {...rest}
        ref={ref}
        selected={!isPreview && !isDisabled && isSelected}
        onClick={toggleSelect}
        size={usedWidth + units}
        lettersize={fontSize}
      >
        {isDisabled && <DisabledOverlay/>}
        <MagicCardPrimaryAction disabled={isDisabled}>
          <CardPicture
            sixteenByNine={!isSquare}
            square={isSquare}
            image={cardUrl}
          />
          <CardInfoWrapper>
            <CardName use='overline' tag='h3'>
              {title}
            </CardName>
            <CardSubtitle use='caption' tag='h4'>
              {subtitle}
            </CardSubtitle>
            <CardDescription use='body2' tag='p'>
              {description}
            </CardDescription>
          </CardInfoWrapper>
        </MagicCardPrimaryAction>
        {/*TODO: Maybe i dont need this, remove it if you dont use it for a while*/}
        {/*{!isPreview &&*/}
        {/*(*/}
        {/*  <MagicCardActions>*/}
        {/*    <MagicCardActionsButtons>*/}
        {/*      <MagicCardActionsButton>*/}
        {/*        Play*/}
        {/*      </MagicCardActionsButton>*/}
        {/*    </MagicCardActionsButtons>*/}
        {/*  </MagicCardActions>*/}
        {/*)}*/}
      </CardWrapper>
    )
  })

MagicCard.propTypes = {
  card: PropTypes.oneOf(['BLIZZARD_STORM', 'DESTINY_CREATOR']).isRequired,
  size: PropTypes.shape({
    width: PropTypes.number.isRequired,
    units: PropTypes.oneOf(['px', '%', 'vw', 'em', 'rem']).isRequired,
  }),
  fontSize: PropTypes.number, // font size in px
  ratio: PropTypes.oneOf(['sixteenByNine', 'square']),
  isPreview: PropTypes.bool,
}

MagicCard.defaultProps = {
  size: {
    width: 13,
    units: 'em',
    __NOT_PROP__: true,
  },
  fontSize: 18,
  ratio: 'sixteenByNine',
  isPreview: false,
}

export default MagicCard