import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { hideDrawer } from 'reducers/ui'
import { ThemeProvider } from '@rmwc/theme'

import '@material/drawer/dist/mdc.drawer.css'

import {
  DrawerWrapper,
  StyledDrawer,
  StyledDrawerContent,
  StyledDrawerHeader,
  StyledDrawerSubTitle,
  StyledDrawerTitle
} from './styles'

const MaterialDrawer = React.forwardRef((props, ref) => {
  const dispatch = useDispatch()
  const store = useSelector(({ ui: { drawer } }) => ({
    drawer,
  }))
  const { theme } = props
  let { drawer: { isOpen, title, subtitle } } = store
  if ( props.title ) title = props.title
  if ( props.subtitle ) subtitle = props.subtitle

  function dispatchClose() {
    dispatch(hideDrawer())
  }

  return (
    <DrawerWrapper mytheme={theme}>
      <ThemeProvider options={theme}>
        <StyledDrawer
          modal
          ref={ref}
          open={isOpen}
          onClose={dispatchClose}
          elevation={20}
        >
          <StyledDrawerHeader>
            {
              title &&
              <StyledDrawerTitle>{title}</StyledDrawerTitle>
            }
            {
              subtitle &&
              <StyledDrawerSubTitle>{subtitle}</StyledDrawerSubTitle>
            }
          </StyledDrawerHeader>
          <StyledDrawerContent>
            {props.children}
          </StyledDrawerContent>
        </StyledDrawer>
      </ThemeProvider>
    </DrawerWrapper>

  )
})

export default MaterialDrawer