import PropTypes from 'prop-types'

export const GameFieldPropTypes = {
  onUserAction: PropTypes.func.isRequired,
  announces: PropTypes.shape({
    availableAnnounces: PropTypes.shape({
      clubs: PropTypes.bool.isRequired,
      diamonds: PropTypes.bool.isRequired,
      hearts: PropTypes.bool.isRequired,
      spades: PropTypes.bool.isRequired,
      'no trump': PropTypes.bool.isRequired,
      'all trump': PropTypes.bool.isRequired,
    }).isRequired,
    availableMagicCards: PropTypes.shape({
      'BLIZZARD_STORM': PropTypes.bool.isRequired,
      'DESTINY_CREATOR': PropTypes.bool.isRequired,
    })
  }),
  cards: PropTypes.arrayOf(
    PropTypes.shape({
      suit: PropTypes.oneOf([ 'clubs', 'diamonds', 'hearts', 'spades' ]).isRequired,
      cardValue: PropTypes.oneOf([ 7, 8, 9, 10, 'J', 'Q', 'K', 'A' ])
    })
  )
}

export const GameFieldDefaultProps = {
  announces: null,
  cards: null,
}
