import React from 'react'
import { MagicCard } from 'modules/belot'
import {
  MagicCardsContainer,
  RulesBody,
  RulesDescription,
  RulesPageTitle,
  RulesPageWrapper,
} from './styles'

export const RulesPage = () => (
  <RulesPageWrapper classNmae='mdc-theme--background'>
    <RulesPageTitle
      use='headline2'
      tag='h1'
      theme='onSurface'
    >
      Magic belot rules
    </RulesPageTitle>
    <RulesBody>
      <RulesDescription
        use='body1'
        theme='onSurface'
      >
        The difference between a normal belot and <b>MAGIC</b> belot, is
        mainly the
        presence of <i>magic cards`</i>, that removes the luck from the game
        There are 2 magic cards:
      </RulesDescription>
      <MagicCardsContainer>
        <MagicCard card='DESTINY_CREATOR' isPreview ratio='square' size={{width:25, units:'%'}} fontSize={22}/>
        <MagicCard card='BLIZZARD_STORM' isPreview ratio='square'  size={{width:25, units:'%'}} fontSize={22}/>
      </MagicCardsContainer>
    </RulesBody>
  </RulesPageWrapper>
)

export default RulesPage