import React from 'react'
import styled from 'styled-components'
import { IconButton } from '@rmwc/icon-button'
//TODO: Import styles when spiting projects

export const GameFieldWrapper = styled.div`
  position:relative;
  display:flex;
  flex-wrap:wrap;
  align-items:center;
  height:100vh;
  max-height:1000px;
  max-width:100vw;
  overflow:hidden;
  // border:1px solid black;
  background:#DFDFDF;
  // & > * {
  // border:1px solid black;
  // }
`


const HorizontalHand = styled.div`
  display:flex;
`

const VerticalHand = styled.div`
  width:100%;
  transform-origin:50%;
  transform:rotateZ(90deg);
  position:absolute;
  display:flex;
`

export const PartnerHand = styled(HorizontalHand)`
  align-self:flex-start;
  flex: 0 0 100%;
`

export const OwnHand = styled(HorizontalHand)`
  flex:1;
  align-self:flex-end;
`

export const RightPlayerHand = styled(VerticalHand)`
  left:40%; 
`
export const LeftPlayerHand = styled(VerticalHand)`
  left:-40%;
`

export const FullScreenButton = styled(IconButton)`
  position:absolute!important;
  width:60px!important;
  height:60px;!important;
  z-index:5;
  top:30px;
  right:30px;
  fill:inherit!important
  & > svg {
    position:relative;
    left:-6px;
    top:-11px;
  }
`

