export const UPDATE_THEME = 'UPDATE_THEME'

export const updateTheme = theme => ({
  type: UPDATE_THEME,
  payload: theme
})

const initialState = {
  theme: 'material'
}

export default (state = initialState, action) => {
  switch ( action.type ) {
    case UPDATE_THEME:
      return {
        ...state,
        theme: action.payload,
      }
    default :
      return state
  }
}