import React from 'react'
import { formatDate } from 'utils/dateUtils'

import {
  SingleNewWrapper,
  NewsTitle,
  NewsDate,
  NewsListItem,
  NewsListView,
} from './styles'

const SingleNew = ({ data }) => {
  const { title, dateCreated, content = [] } = data

  const renderContent = () =>
    content.map((listItem, index) => (
      <NewsListItem
        key={index}
        text={listItem}
        graphic='forward'
        ripple={false}
        disabled
      />
    ))

  return (
    <SingleNewWrapper>
      <NewsTitle use='headline6' theme={['onSurface']}>
        {title}
      </NewsTitle>
      <NewsDate use='caption' theme={['onSurface']}>
        {formatDate(dateCreated)}
      </NewsDate>
      <NewsListView>
        {renderContent()}
      </NewsListView>
    </SingleNewWrapper>
  )
}

export default SingleNew