import React, { useContext, useEffect, useState } from 'react'
import ThemeContext from 'context/ThemeContext'
import SinglePost from './SinglePost'
import PostTooltip from './PostTooltip'

import { PostsDivider, PostsList, PostsTitle, PostsWrapper, Scroll } from './styles'
import '@material/elevation/dist/mdc.elevation.css'

const FAKE_POSTS = require('./stubs/fake-posts.json')
// const ITEMS_PER_PAGE = 5

const PostsFeed = props => {
  const theme = useContext(ThemeContext)
  const [ posts, setPosts ] = useState([])
  // const [ page, setPage ] = useState(0)
  // const scrollRef = useRef()
  // const fetchPosts = page => new Promise(resolve =>
  //   setTimeout(() => {
  //     resolve(FAKE_POSTS.slice(page * ITEMS_PER_PAGE, page* ITEMS_PER_PAGE + ITEMS_PER_PAGE))
  //   }), 3000)


  useEffect(() => {
    setPosts(FAKE_POSTS)

    return () => setPosts(null)
  }, [])

  //TODO: Finish when connected to BackEnd
  // function loadMorePosts() {
  //   const nextPage = page + 1
  //   fetchPosts(nextPage).then(data => {
  //     setPage(nextPage)
  //     console.log(posts)
  //     const newPosts = posts.concat(data)
  //     // console.log(newPosts)
  //     setPosts(newPosts)
  //   })
  // }

  function renderPosts() {
    if ( !posts )
      return null

    return posts.map((feed, index) => (
      <PostTooltip key={index} data={feed}>
        <a href={`#${index}`} style={{ textDecoration: 'none' }}>
          <PostsDivider/>
          <SinglePost data={feed}/>
        </a>
      </PostTooltip>
    ))
  }

  return (
    <PostsWrapper z={12} localtheme={theme}>
      <PostsTitle localtheme={theme}>Latest posts in the forum</PostsTitle>
      <Scroll localtheme={theme}>
        <PostsList twoLine>
          {renderPosts()}
        </PostsList>
      </Scroll>
    </PostsWrapper>
  )
}

export default PostsFeed