import styled from 'styled-components'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import { Elevation } from '@rmwc/elevation'
import { List, SimpleListItem } from '@rmwc/list'
import { Button } from '@rmwc/button'

export const Announces = styled.div`
  position:absolute;
  width:100vw;
  height:100vh;
  display:flex;
`

export const Overlay = styled.div`
  position:absolute;
  width:100vw;
  height:100vh;
  top:0;
  left:0;
  background:rgba(0,0,0,0.7);
  z-index:2;
`

export const AnnounceWrapper = styled(Elevation)`
  z-index:3;
  background:#DEDEDE;
  border-radius:10px;
  position:relative;
  padding-bottom: 15px;
  width:30vw;
  max-width:250px;
  margin:-30px auto 0; 
  align-self:center;
`

export const AnnounceList = styled(List)`
`

export const AnnounceSingle = styled(SimpleListItem)`
  height:calc((100vh - (2 * 11vw) - 5vh) / 9 )!important;
  max-height:48px;
  color: ${({ color }) => color}!important;
  & > svg {
    color: ${({ color }) => color}!important;
  }
  user-select:none;
  ${({ disabled }) => disabled && {
  color: '#BBB!important',
}}
  text-transform:capitalize;
`

export const ButtonWrapper = styled.div`
  width:60%;
  margin:auto;
`

export const ConfirmButton = styled(Button)`
  width:100%!important;
`


export const MagicCardHolder = styled(ReactCSSTransitionGroup)`
  // transform:translateX(-40vw);
  z-index:999;
  margin:auto; 
  position:relative;
`