const REQUEST_STARTED = 'REQUEST_STARTED'
const REQUEST_FINISHED = 'REQUEST_FINISHED'


const registerAPI = () => dispatch => {
  return new Promise(resolve => resolve(true))
}

const loginAPI = () => dispatch => {
  return new Promise((resolve, reject) => {
    // Fake loading, TODO: Actual API call
    setTimeout(() => {
      resolve({
        username: 'Stefan',
        profilePhoto: 'https://img.freepik.com/free-vector/businessman-profile-cartoon_18591-58479.jpg?size=338&ext=jpg',
      })
    }, 100)
  })
}
const logoutAPI = () => dispatch =>
  new Promise(resolve => {
    resolve(true)
  })


export const startRegister = data => dispatch => {
  dispatch({
    type: REQUEST_STARTED
  })


  dispatch(registerAPI()).then(() => {
      dispatch({ type: REQUEST_FINISHED })
    }
  )
}

export const startLogin = data => dispatch => {
  dispatch({
    type: REQUEST_STARTED,
  })

  dispatch(loginAPI()).then(result => {
    dispatch({
      type: REQUEST_FINISHED,
      payload: result,
    })
  })
}

export const startLogout = () => dispatch => {
  dispatch({
    type: REQUEST_STARTED,
  })

  dispatch(logoutAPI()).then(() => {
    dispatch({
      type: REQUEST_FINISHED,
      payload: null,
    })
  })
}

const initialState = {
  loggedInUser: null,
  isLoading: false,
}
export default (state = initialState, action) => {
  switch ( action.type ) {
    case REQUEST_STARTED :
      return {
        ...state,
        isLoading: true,
      }
    case REQUEST_FINISHED :
      return {
        ...state,
        isLoading: false,
        loggedInUser: action.payload,
      }
    default :
      return state

  }
}


