import React from 'react'
import { BriefInfo, NewsFeed, PostsFeed } from './components/'

import {
  ContentMain,
  ContentMainLeft,
  ContentMainRight,
  ContentWrapper,
  Heading1,
  Heading2,
  PlayButtonWrapper,
} from './styles'

const INFO_STUB = require('./mainpageinfo.json')

const MainPage = () => (
  <ContentWrapper>
    <ContentMain>
      <ContentMainLeft>
        <Heading1 use='headline1' theme={[ 'textPrimaryOnBackground' ]}>
          Magic Material Belot
        </Heading1>
        <Heading2 use='headline4' theme={[ 'textHintOnBackground' ]}>
          Belot with 'magic' designed using material design guidelines
        </Heading2>
        <BriefInfo info={INFO_STUB}/>
        <NewsFeed/>
      </ContentMainLeft>
      <ContentMainRight>
        <PostsFeed/>
      </ContentMainRight>
    </ContentMain>
  </ContentWrapper>
)

export default MainPage