const SHOW_DRAWER = 'SHOW_DRAWER'
const HIDE_DRAWER = 'HIDE_DRAWER'
const TOGGLE_NAV_IS_FIXED = 'TOGGLE_NAV_FIXED'


export const showDrawer = (title, subtitle) => ({
  type: SHOW_DRAWER,
  payload: {
    title,
    subtitle,
  }
})

export const hideDrawer = () => ({
  type: HIDE_DRAWER
})

export const toggleNavIsFixed = isFixed => ({
  type: TOGGLE_NAV_IS_FIXED,
  payload: isFixed,
})

const initialState = {
  drawer: {
    isOpen: false,
  },
  navigation: {
    isFixed: false,
  }
}
export default (state = initialState, action) => {

  switch ( action.type ) {
    case SHOW_DRAWER :
      return {
        ...state,
        drawer: {
          ...action.payload,
          isOpen: true,
        }
      }
    case HIDE_DRAWER:
      return {
        ...state,
        drawer: {
          isOpen: false,
        }
      }
    case TOGGLE_NAV_IS_FIXED:
      return {
        ...state,
        navigation: {
          isFixed: action.payload || !state.navigation.isFixed
        }
      }
    default :
      return state
  }
}