import React, { useEffect, useState } from 'react'
import {
  ThemePickerInner,
  ThemePickerItem,
  ThemePickerItemMeta,
  ThemePickerItems,
  ThemePickerItemSwitch,
  ThemePickerWrapper
} from './styles'

const THEMES_CHECKED = {
  default: false,
  dark: false,
  material: false,
  crane: false,
}

const ThemePicker = ({ toggle, isOpen, onClose, currentTheme, theme }) => {
  const [ selected, setSelected ] = useState({ ...THEMES_CHECKED, [currentTheme]: true }) // default dark

  useEffect(() => {
      setSelected({ ...THEMES_CHECKED, [currentTheme]: true })
    },
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [ isOpen ])

  function selectTheme(theme) {
    setSelected({ ...THEMES_CHECKED, [theme]: !selected[theme] })
  }

  function close() {
    onClose(Object.keys(selected).find(item => selected[item]))
  }

  const renderItems = () => (
    Object.keys(THEMES_CHECKED).map(themeOption => (
      <ThemePickerItem
        localtheme={theme}
        key={themeOption}
        activated={selected[themeOption]}
        onClickCapture={() => selectTheme(themeOption)}
      >
        {themeOption.toLocaleUpperCase()}
        <ThemePickerItemMeta>
          <ThemePickerItemSwitch
            checked={selected[themeOption]}
            readOnly
          />
        </ThemePickerItemMeta>
      </ThemePickerItem>
    ))
  )

  return (
    <ThemePickerWrapper>
      <ThemePickerInner
        fixed
        anchorCorner='bottomRight'
        open={isOpen}
        onClose={close}
      >
        <ThemePickerItems>
          {renderItems()}
        </ThemePickerItems>
      </ThemePickerInner>
      {toggle}
    </ThemePickerWrapper>
  )
}

export default ThemePicker