import styled from 'styled-components'
import { MenuItem, MenuItems, MenuSurface, MenuSurfaceAnchor } from '@rmwc/menu'
import { Switch } from '@rmwc/switch'
import { ListItemMeta } from '@rmwc/list'

import '@material/switch/dist/mdc.switch.css'
import '@material/form-field/dist/mdc.form-field.css'

export const ThemePickerWrapper = styled(MenuSurfaceAnchor)`
`

export const ThemePickerInner = styled(MenuSurface)`
  padding:10px 0!important;

`

export const ThemePickerItems = styled(MenuItems)`
  width:150px;
`

export const ThemePickerItem = styled(MenuItem)`
  user-select:none;
  &.mdc-list-item--activated {
    color:${({ localtheme }) => localtheme.secondary || 'teal' /*teal is material secondary*/}!important;
  }
`

export const ThemePickerItemMeta = styled(ListItemMeta)`
`

export const ThemePickerItemSwitch = styled(Switch)`
`
