import React from 'react'
import { ANNOUNCE } from '../../utils/ActionType'
import GameField from './GameField'

const INITIAL_ANNOUNCES = require('../../API/stubs/announces.json')
const INITIAL_CARDS = require('../../API/stubs/start_hand.json')
const NEXT_THREE = require('../../API/stubs/next_three.json')

export default class GameController extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      currentAnnounce: INITIAL_ANNOUNCES,
      currentCards: INITIAL_CARDS,
    }

    this.handleUserAction = this.handleUserAction.bind(this)
  }

  handleUserAction = action => {
    // This will be transferred to the backend
    const { actionType } = action
    switch (actionType) {
      case ANNOUNCE : {
        this.setState({ ...this.state, currentAnnounce: null })
        const { announceType } = action
        const { currentAnnounce } = this.state
        let isFound = false
        const { availableAnnounces, availableMagicCards } = currentAnnounce
        Object.keys(availableAnnounces).forEach(announce => {
          availableAnnounces[announce] = isFound
          if (announce === announceType)
            isFound = true
        })
        // setTimeout(() => {
        this.setState({
          currentAnnounce: null,
          currentCards: this.state.currentCards.concat(NEXT_THREE),
        })
        // }, 1500)
      }
        break
      default: {
        throw new Error('WTF MAN?! Wrong actionType')
      }
    }
  }

  render () {

    return (
      <GameField
        announces={this.state.currentAnnounce}
        cards={this.state.currentCards}
        onUserAction={this.handleUserAction}
      />
    )
  }
}